import React from 'react';
import { Helmet } from 'react-helmet';

export default function PolitiqueConfidentialite() {
  return (
    <main className="bg-[#181B20] min-h-screen text-white font-sans">
      <Helmet>
        <title>Politique de confidentialit - NovaWeb</title>
        <meta name="description" content="Politique de confidentialit du site NovaWeb." />
      </Helmet>
      <section className="max-w-3xl mx-auto py-16 px-6">
        <h1 className="text-3xl font-bold mb-8 text-center">Politique de confidentialit</h1>
        <div className="space-y-8 text-gray-300">
          <div>
            <h2 className="text-xl font-semibold mb-2 text-white">Collecte des donnes personnelles</h2>
            <p>Nous collectons uniquement les informations ncessaires au traitement de vos demandes (nom, email, message via le formulaire de contact). Aucune donne n'est transmise  des tiers sans votre consentement.</p>
          </div>
          <div>
            <h2 className="text-xl font-semibold mb-2 text-white">Utilisation des donnes</h2>
            <p>Les donnes collectes servent uniquement  rpondre  vos demandes et  amliorer nos services. Elles ne sont jamais utilises  des fins commerciales sans votre accord.</p>
          </div>
          <div>
            <h2 className="text-xl font-semibold mb-2 text-white">Cookies</h2>
            <p>Le site peut utiliser des cookies pour amliorer l'exprience utilisateur. Vous pouvez configurer votre navigateur pour refuser les cookies.</p>
          </div>
          <div>
            <h2 className="text-xl font-semibold mb-2 text-white">Droits de l'utilisateur</h2>
            <p>Conformment au RGPD, vous disposez d'un droit d'accs, de rectification et de suppression de vos donnes. Pour exercer ces droits, contactez-nous  <a href="mailto:contact@novaweb.fr" className="text-blue-400 underline">contact@novaweb.fr</a>.</p>
          </div>
          <div>
            <h2 className="text-xl font-semibold mb-2 text-white">Contact</h2>
            <p>Pour toute question relative  la protection de vos donnes, contactez-nous  l'adresse ci-dessus.</p>
          </div>
        </div>
      </section>
    </main>
  );
} 